<template>
    <yd-layout>
        <div class="schedules">
            <div class="handleTabBox">
                <!-- <div class="search" v-if="focusFlag">
                    <input type="text" name="searchWord" class="searchWord" placeholder="搜索" @focus="focusFlag = false">
                </div> -->
                <div class="search" :class="{active: searchFlag}">
                    <input v-show="focusFlag" type="text" v-model="name1" name="searchWord" class="searchWord" @focus="searchFlag = true; orgQueryList1= []" @keyup.enter="hasSearch(name1)" placeholder="搜索">
                    <input v-show="!focusFlag" type="text" v-model="name" name="searchWord" class="searchWord" @focus="searchFlag = true; orgQueryList= []" @keyup.enter="search(name)" placeholder="搜索">
                    <span v-show="searchFlag" @click="searchFlag = false;name='';name1='';orgQueryList1=[];orgQueryList=[];" class="cancel">取消</span>
                    <div v-show="searchFlag" @click="name='';name1='';" class="close">
                        <img src="../../../static/images/workCenter/cancel.png">
                    </div>
                </div>
                <div class="tabBottom">
                    <span @click="focusFlag = true" class="tabIcon" :class="{active: focusFlag==true}">已关注</span>
                    <span @click="focusFlag = false;treeFlag=true" class="tabIcon" :class="{active: focusFlag==false}">未关注</span>
                </div>
            </div>
            <div class="hasFocus" v-show="focusFlag">
                <div class="searchContent" @click="searchFlag = false; name1=''" v-if="searchFlag">
                    <ul>
                        <router-link v-for="item in orgQueryList1" :to="'/mySchedule/'+ item.id +'/1/'+item.name" :key="item.dept">
                            <li class="searchLi" v-text='item.name+" "+ item.dept'>
                            </li>
                        </router-link>
                    </ul>
                </div>
                <yd-flexbox v-show="!searchFlag" v-for="(item, index) in otherList" :key="item.id">
                    <yd-flexbox-item>
                        <router-link :to="'/mySchedule/'+ item.id +'/1/'+item.name">
                            <div class="face">
                                <img src="../../../static/images/m1.png" v-show="item.gender=='2'">
                                <img src="../../../static/images/m2.png" v-show="item.gender=='1'">
                            </div>
                            <div class="desc">
                                <div class="name" v-text="item.name"></div>
                                <div class="position" v-text="item.dept"></div>
                                <!-- <div class="position">最近更新了一条日程</div> -->
                            </div>
                        </router-link>
                        <div class="unsubscribe" align="center" @click="cancelFocus(item.id, $event)">
                            <img src="../../../static/images/schedule/quxiaoguanzhu.png" alt="">
                            <div class="txt">取消关注</div>
                        </div>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
            <div class="noFocus" v-if="treeFlag" v-show="!focusFlag">
                <div class="searchContent" @click="searchFlag = false; name=''" v-if="searchFlag">
                    <ul>
                        <li @click.stop="addFocus(item.id,$event)" class="searchLi" v-for="item in orgQueryList" v-text='item.text+" "+jsonData[jsonData[item.parent].parent].text+"/"+jsonData[item.parent].text'></li>
                    </ul>
                </div>
                <div  v-show="!searchFlag" class="tree-menu">
                    <trs-tree :type="type" v-on:callbackTop="addFocus"></trs-tree>
                </div>
            </div>
        </div>
        <yd-tabbar class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item title="我的日程" :link="'/mySchedule/' + $store.getters.getUserInfo.empcode + '/0/my'">
                <yd-icon name="mySchedule" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="查看他人日程" link="/viewSchedules/" active>
                <yd-icon name="toView" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import trsTree from '../../components/util/trsTree/trsTree.vue';
export default {
    name: 'viewSchedules',
    components: {
        trsTree
    },
    data() {
        return {
            bottomPopup: false,
            theModel: [],
            jsonData: [],
            name: '',
            name1: '',
            type: 'schedule',
            focusFlag: true,
            searchFlag: false,
            orgQueryList: [],
            orgQueryList1: [],
            otherList: [],
            treeFlag: false
        }
    },
    watch: {
        name(val) {
            this.search(val);
        },
        name1(val) {
            this.hasSearch(val);
        }
    },
    mounted() {
        document.setTitle('查看他人日程');
        this.pageRender();
    },
    methods: {
        pageRender() {
            // 已关注
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/attentionList', {
                'ssotoken': vm.$store.getters.getUserInfo.empcode
            }, function (res) {
                vm.otherList = res.jsonData;
            });
            // 组织结构未   关注列表
            vm.get(vm.conf.schedule + '/mobileInterface/orgTree', {
                'ssotoken': vm.$store.getters.getUserInfo.empcode
            }, function (res) {
                var childrenArr = res.jsonData[205].children.split(';');
                vm.jsonData = res.jsonData;
                for (var i = 0; i < childrenArr.length; i++) {
                    var chnl = childrenArr[i];
                    var childrens = res.jsonData[chnl];
                    if (childrens != undefined) {
                        vm.theModel.push(childrens);
                    }
                }
            });
        },
        addFocus(id) {
            console.log('父亲1');
            var vm = this;
            // 查看添加关注
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/addAttention', {
                'ssotoken': vm.$store.getters.getUserInfo.empcode,
                'otherEmpcode': id.toString()
            }, function (res) {
                let promise = new Promise(function (resolve, reject) {
                    vm.$dialog.toast({
                        mes: '关注成功',
                        timeout: 1000
                    });
                    setTimeout(function () {
                        resolve();
                    }, 1500);
                });
                promise.then(function () {
                    vm.pageRender();
                    vm.searchFlag = false;
                    vm.name = '';
                });
            })
        },
        search(name) {
            var key;
            var vm = this;
            if (name == '') {
                vm.orgQueryList = [];
                return;
            }
            console.log(0)
            var promise = new Promise(function (resolve, reject) {
                vm.orgQueryList = [];
                for (key in vm.jsonData) {
                    var r = vm.jsonData[key].text.match(new RegExp(name));
                    if (r != null) {
                        vm.orgQueryList.push(vm.jsonData[key]);
                    }
                }
            });
            promise.then();
        },
        hasSearch(name) {
            const vm = this;
            if (name == '') {
                vm.orgQueryList1 = [];
                return;
            }
            console.log(1)
            vm.orgQueryList1 = [];
            vm.otherList.forEach((element, index) => {
                let r = vm.otherList[index].name.match(new RegExp(name));
                if (r != null) {
                    vm.orgQueryList1.push(vm.otherList[index]);
                }
            });
        },
        cancelFocus(id, e) {
            // 取消关注
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/cancelAttention', {
                'ssotoken': vm.$store.getters.getUserInfo.empcode,
                'otherEmpcode': id.toString()
            }, function (res) {
                vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/attentionList', {
                    'ssotoken': vm.$store.getters.getUserInfo.empcode
                }, function (res) {
                    vm.otherList = res.jsonData;
                });
                vm.$dialog.toast({
                    mes: '取消关注成功',
                    timeout: 1000
                });
            });
            e.stopPropagation || e.stopPropagation();
        }
    }
}
</script>
<style lang="less" scoped>
.handleTabBox {
    height: 2.2rem;
    padding-top: .18rem;
}

.schedules .search {
    background: #fff;
    padding: .11rem .3rem;
    .searchWord {
        background-color: #f3f3f3;
        background-position: .27rem;
        border-radius: .08rem;
        text-align: left;
        padding-left: .66rem;
    }
}

.noFocus {
    margin-top: .2rem;
}

.face {
    width: 1.28rem;
    height: 1.28rem;
    // border-radius: 1000px;
    // border: 1px solid #EFEEEC;
    position: absolute;
    top: .3rem;
}
.position{
    width: 3.7rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.yd-tab-panel {
    background: transparent;
}

.desc {
    padding: .2rem 0;
    padding-left: 1.5rem;
    height: 2.14rem;
}

.schedules .searchContent {
    position: absolute;
    top: 1.2rem;
    left: 0;
    width: 100%;
    height: auto;
    min-height: 100%;
    background: #fff;
    z-index: 99;
}

.searchLi {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px dotted #ccc;
    padding-left: 0.8rem;
}

.name {
    color: #333333;
    font-size: .3rem;
    margin-bottom: .1rem;
    line-height: .8rem;
}

.face img {
    width: 100%;
}

.office,
.desc {
    font-size: .24rem;
    line-height: .44rem;
}

.yd-flexbox {
    padding: 0 .14rem;
    position: relative;
    .yd-flexbox-item {
        background: #fff;
        margin-top: .2rem;
        border-radius: .1rem;
        height: 1.9rem;
        padding-left: .38rem;
        position: relative;
    }
}

.unsubscribe,
.attention {
    position: absolute;
    top: .54rem;
    right: .3rem;
    line-height: .45rem;
}

.unsubscribe {
    color: #ff3b5f;
}

.unsubscribe img,
.attention img {
    width: .5rem;
}

.attention {
    color: #ffa21d;
}
</style>


